class Clock extends React.Component {
  constructor(props) {
    super(props);
    this.state = { date: new Date() };
  }
  render() {
    return (
      <div>
        <h1>Aktuelle Zeite {new Date().toLocaleTimeString()}</h1>
      </div>
    );
  }
  componentDidMount() {
    this.ticker = setInterval(() => this.tick(), 1000);
  }
  tick() {
    this.setState({
      date: new Date(),
    });
  }
}

class MyMagicButton extends React.Component {
  constructor(props) {
    super(props);
    this.state = { render: true };
  }
  deleteMe() {
    this.setState({
      render: false
    })
  }

  render() {
    if (this.state.render) {
      return <button onClick={(e) => this.deleteMe(e)}>Delete me</button>;
    }
  }
}

ReactDOM.render(
  <React.StrictMode>
    // eslint-disable-next-line
    {/* <App /> */}
    <Clock />
    <MyMagicButton/>
  </React.StrictMode>,
  document.getElementById('root')
);

